"use client";

import Image from "@/public/Sri-Lions-Bevellled-Logo.png";
import Link from "next/link";
import { useState } from "react";

export default function Navbar() {
  const [isSportsOpen, setIsSportsOpen] = useState(false);

  // Define sports and their page routes
  const sports = [
    { name: "Rugby", path: "/sports/rugby" },
    { name: "Netball", path: "/sports/netball" },
    { name: "Cricket", path: "/sports/cricket" },
    { name: "Golf", path: "/sports/golf" },
    { name: "Other", path: "/sports/other" },
    { name: "Other2", path: "/sports/other2" },
    { name: "Other3", path: "/sports/other3" },
    { name: "Other4", path: "/sports/other4" },
    { name: "Other5", path: "/sports/other5" },
    { name: "Other6", path: "/sports/other6" },
  ];

  return (
    <nav className="bg-[#480202] text-white shadow-md">
      <div className="container mx-auto flex items-center justify-between px-4 py-3">
        {/* Left side - Club Logo */}
        <div className="flex items-center">
          <Link href="/">
            <Image
              src="Sri-Lions-Bevellled-Logo.png" // ðŸ–¼ï¸ place logo.png inside /public
              alt="Sri Lions Club Logo"
              width={60}
              height={60}
              className="cursor-pointer"
            />
          </Link>
        </div>

        {/* Right side - Navigation Links */}
        <ul className="hidden md:flex space-x-8 text-[17px] font-medium items-center">
          <li><Link href="/" className="hover:text-gray-300">Home</Link></li>
          <li><Link href="/news" className="hover:text-gray-300">News</Link></li>

          {/* Sports Dropdown */}
          <li
            className="relative"
            onMouseEnter={() => setIsSportsOpen(true)}
            onMouseLeave={() => setIsSportsOpen(false)}
          >
            <button className="hover:text-gray-300 flex items-center">
              Sports â–¾
            </button>

            {isSportsOpen && (
              <ul className="absolute bg-[#5a0a0a] top-full mt-2 py-2 w-44 rounded-md shadow-lg z-50">
                {sports.map((sport) => (
                  <li key={sport.name}>
                    <Link
                      href={sport.path}
                      className="block px-4 py-2 hover:bg-[#661010] transition"
                    >
                      {sport.name}
                    </Link>
                  </li>
                ))}
              </ul>
            )}
          </li>

          <li><Link href="/events" className="hover:text-gray-300">Events</Link></li>
          <li><Link href="/about" className="hover:text-gray-300">About Us</Link></li>
          <li><Link href="/contact" className="hover:text-gray-300">Contact Us</Link></li>
        </ul>

        {/* Mobile menu button placeholder */}
        <div className="md:hidden">
          {/* We'll add mobile navigation later */}
        </div>
      </div>
    </nav>
  );
}
